@media all and (min-width: 280px) {
  .reset {
    padding: 60px 0;
    background-color: #fafafa;
  }
  .reset-password form {
    padding: 60px 0;
  }

  .reset form {
    padding: 30px 30px;

    border-radius: 8px;
    border-style: solid;
    border-width: 2px;
    border-color: rgba(227, 235, 253, 1);
