.create-post {
  width: 100%;
}
#uploadbutton {
  position: relative;
  overflow: hidden;
  font-family: "Palatino Linotype", "Book Antiqua", Palatino, serif !important;
  font-weight: bold;
  font-size: 15px;
  background-color: #fafafa;
}

#file {
  position: absolute;
  font-size: 25px;
  opacity: 0;
  right: 0;
  top: 0;
}
#message {
  width: 750px;
  height: 75px;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  resize: none;
  margin-left: auto;
  margin-right: auto;
  white-space: normal;
  word-wrap: break-word;
  display: block;
  background-color: #fafafa;

  border-radius: 8px;
  border-style: solid;
  border-width: 1.5px;
  border-color: rgba(227, 235, 253, 0.8);
}

.div-container {
  width: 282px;
  /* margin-right: 0px;
  margin-left: auto; */
  /* padding: 0px 0px; */

  border-radius: 8px;
  border-style: solid;
  border-width: 2px;
  border-color: rgba(227, 235, 253, 1);
}

.buttons-container button {
  display: inline;
  margin-right: 0;
  margin-left: auto;
  font-family: "Palatino Linotype", "Book Antiqua", Palatino, serif !important;
}

.buttons-container {
  width: 750px;
  margin-left: auto;
  margin-right: auto;
}

#post {
  background-color: #fafafa;
  padding: 6px 10px;
  font-family: sans-serif;
  font-weight: bold;
}

#tag {
  background-color: #fafafa;
  padding: 6px 10px;
  font-family: sans-serif;
  font-weight: bold;
}

.close {
  color: Black;
  float: right;
}

.close:hover {
  color: red;
  cursor: pointer;
}

#modalcontent {
  border-radius: 8px;
  border-style: solid;
  border-width: 2.5px;
  border-color: rgba(227, 235, 253, 0.8);
}

.modal {
  z-index: 2;
  display: none;
  font-weight: bold;

  position: fixed; /* Stay in place */
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  font-family: sans-serif;
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

#clear {
  margin-right: 7px;
}

.modal-title {
  font-weight: bold;
}

#hashtaginput {
  margin-top: 10px;
  margin-bottom: 5px;
  width: 100%;
  height: 100px;
  font-size: 15px;
  border: 1px solid rgba(227, 235, 253, 0.8);
}

#hashtagsubmit {
  font-family: sans-serif;
  margin-top: 10px;
  width: 75px;
}

.clear-btn {
  margin-top: 10px;
  margin-left: 10px;
  width: 170px;
}

#upload {
  background-color: #fafafa;
  padding: 6px 10px;
  font-family: sans-serif;
  font-weight: bold;
}

#pic {
  width: 20px;
  height: 20px;
}

#postan {
  background-color: #fafafa;
  padding: 6px 10px;
  font-family: sans-serif;
  font-weight: bold;
}

@media only screen and (max-width: 1100px) {
  #message {
    max-width: 732px;
    width: 100%;
  }

  .buttons-container {
    max-width: 732px;
    width: 100%;
  }
}
